@import url('https://fonts.googleapis.com/css2?family=DynaPuff&family=Montserrat:ital@1&display=swap');
.App {
  font-family: "Ubuntu", sans-serif;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

#light {
  background-color: #31ff65;
}

#dark {
  background-color: rgb(0, 242, 255);
}

body {
  margin: 0;
  padding: 0;
}

body .main {
  width: 400px;
  height: 430px;
  border-radius: 1.9em;
  
}

#light .main {
  background-color: #fff;
  border-color: #fff;
  border-radius: 20px;
  box-shadow: 1px 2px 14px 1px rgb(255, 0, 166);
}

#light .Footer {
  color: #8c55aa;
}
#dark .main {
  background-color: #cf8cf8;
  border-color: #fff;
  border-radius: 30px;
  box-shadow: 1px 2px 14px 1px rgb(0, 26, 255);
}

.sign {
  padding-top: 40px;
  font-family: 'DynaPuff', cursive;
font-family: 'Montserrat', sans-serif;
  font-weight: bold;
  font-size: 28px;
  margin-top: 0px;
}

#light .sign {
  color: #aa5555;
}

#dark .sign {
  color: #ffffff;
}

.username {
  width: 76%;
  font-weight: 700;
  font-size: 14px;
  letter-spacing: 1px;
  padding: 10px 20px;
  border: none;
  border-radius: 20px;
  outline: none;
  box-sizing: border-box;
  border: 2px solid rgba(0, 0, 0, 0.02);
  margin-bottom: 50px;
  margin-left: 46px;
  text-align: center;
  margin-bottom: 27px;
  font-family: "Ubuntu", sans-serif;
  margin-top: 0px;
}

form.form1 {
  padding-top: 40px;
}

.password {
  width: 76%;
  font-weight: 700;
  font-size: 14px;
  letter-spacing: 1px;
  padding: 10px 20px;
  border: none;
  border-radius: 20px;
  outline: none;
  box-sizing: border-box;
  border: 2px solid rgba(0, 0, 0, 0.02);
  margin-bottom: 50px;
  margin-left: 46px;
  text-align: center;
  margin-bottom: 27px;
  font-family: "Ubuntu", sans-serif;
}

#light .username,
#light .password {
  color: rgb(38, 50, 56);
  background: rgba(136, 126, 126, 0.04);
}

#dark .username,
#dark .password {
  color: rgb(254, 255, 255);
  background: rgba(255, 247, 247, 0.299);
}

.username:focus,
.password:focus {
  border: 2px solid rgba(0, 0, 0, 0.18) !important;
}

.submit {
  cursor: pointer;
  border-radius: 5em;
  color: #fff;
  background: linear-gradient(to right, #9c27b0, #e040fb);
  border: 0;
  padding-left: 40px;
  padding-right: 40px;
  padding-bottom: 10px;
  padding-top: 10px;
  font-family: "Ubuntu", sans-serif;
  margin-left: 35%;
  font-size: 13px;
  box-shadow: 0 0 20px 1px rgba(0, 0, 0, 0.04);
}

.submit:hover {
  background-color: #46045e;
  color: wheat;
  transition: ease-out 0.2s;
  cursor: pointer;
}

.forgot {
  text-shadow: 0px 0px 3px rgba(117, 117, 117, 0.12);
  color: #e1bee7;
  padding-top: 15px;
}

a {
  text-shadow: 0px 0px 3px rgba(117, 117, 117, 0.12);
  color: #e1bee7;
  text-decoration: none;
}

@media (max-width: 600px) {
  .main {
    border-radius: 0px;
  }
}

.switch {
  display: flex;
  width: 100vw;
  height: 100px;
  justify-content: center;
  align-items: center;
}

.switch label {
  margin: 10px;
}

#light .switch label {
  color: black;
}

#dark .switch label {
  color: white;
}

.Footer {
  align-items: center;
  justify-content: center;
  padding-top: 20px;
  color: rgb(17, 0, 255);
  font-size: 20px;
}

#light.Color12 {
  font-size: medium;
  font-weight: bold;
  font-size: 30px;

}
.username{
  text-align: center;
  font-weight: 400px;

}

#Container {
  background-color: #ff6200;
  display: flex;
  flex-direction: row;
  width: 100%;
  border-color: rgb(106, 0, 255);
  border-radius: 5px;
  
}
#Container ul{
  display: flex;
  flex-direction: row;
  list-style-type: none;
  color: white;
}

#Container ul li{
  padding-left: 15px;
  padding-right: 10px;

  cursor: pointer;
  font-size: 20px;
}

#Container ul li:hover{

  transition: ease-out 0.3s;
  background-color: #fff;
  color: black;
  border: 30px;
  border-radius: 20px;
  border-color: #31ff65;

}

.Color{
  color: #ff4343;
}
#Hello{
  font-size: 30px;
  font-weight: bold;
  color: #e1bee7;
  border-radius: 10px;
  font-family: 'Montserrat', sans-serif;
  cursor: pointer;
}